@import url('https://fonts.googleapis.com/css?family=Lobster|Montserrat|Raleway|Permanent+Marker&display=swap');

@font-face {
    font-family: productsFont;
    src: url('/fonts/Product\ Sans\ Regular.ttf');
  }

* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

    
li{
    display: flex;
    justify-content: center; /* For horizontal alignment */
    align-items: center; /* For vertical alignment */
    width: 25%;
    height: 100%;
}

.nav-home{
    background-color: #69D2E7;
}

.nav-about{
    background-color: #A7DBD8;
}

.nav-contact{
    background-color: #FA6900;
}

.nav-projects{
    background-color: #E0E4CC;
}


.nav-list {
    height: 10vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-left: auto;
    list-style: none;
    font-family: 'Permanent Marker', cursive;

}

.nav-links{
    display: flex;
    text-decoration: none;
    color: black;
    font-size: 40px;
    justify-content: center;
}

.home, .about, .contact,.projects{
    height: 90vh;
    position: absolute;
    width: 100%;
    font-family: 'productsFont', sans-serif;
}

.home-content, .about-content, .projects-content{
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    img{
        height: 100%;
    }
    h1{
        font-size: 50px;
        margin: 30px 0px;
    }
}

.home-presentation{
    h1{
        font-size: 180px;
    }
}

.home-skills{
    p{
        transform: rotate(-45deg);
        line-height: 1.5;
    }
}

.color{
    color: #FA6900;
}

.about-photo, .about-presentation {
    display: flex;
    justify-content: center;
    height: 100%;
    width: 50%;
    
}

.about-presentation {
    background-color: #E0E4CC;
    display: flex;
    flex-direction: column;
    align-items: center;
    h1{
        font-size: 80px;
    }
}


.about-text{
    font-size: 25px;
    margin: 0vh 10vh 0vh 10vh;
    text-align: justify;
    font-family: 'Raleway', sans-serif;
}

.contact-content{
    height: 100%;
    display: flex;
    flex-wrap: wrap;  
}

.contact-resume, .contact-email{
    display: flex;
    justify-content: center; /* For horizontal alignment */
    align-items: center; /* For vertical alignment */
    height: 100%;
    width: 50%;
}

.strikeout {
    font-size: 4em;
    line-height: 1em;
    position: relative;
  }
  .strikeout::after {
    border-bottom: 0.125em solid red;
    content: "";
    left: 0;
    margin-top: calc(0.125em / 2 * -1);
    position: absolute;
    right: 0;
    top: 50%;
  }

.change-square{
    width: 400px;
    height: 400px;
    line-height: 370px;
    text-align: center;
    border-radius: 0%;
    border: 15px solid white;
    transition: .2s;
    font-size: 30px;
}

.contact-resume:hover .change-square{
    border-radius: 50%;
    height: 400px;
    line-height: 370px;
}

.contact-email:hover .change-square{
    border-radius: 50%;
    height: 400px;
    line-height: 370px;
}


.contact-resume{
    background-color: #E0E4CC;
}

.contact-resume .change-square{
    border-color: #FA6900;
}

.contact-email .change-square{
    border-color: #E0E4CC;
}

.home{
    background-image: url('/images/Website_stripes.png')
}

.about{
    background-color: #A7DBD8;
}

.contact{
    background-color: #FA6900;
}

.projects{
    background-color: #E0E4CC;
}

@media (max-width: 1000px){

    .home-content{
        flex-direction: column-reverse;
    }

    .home-presentation{
        h1{
            font-size: 150px;
        }
    }

    .home-skills{
        p{
        transform: rotate(0deg);
        font-size: 25px;
        }
    }

    .nav-links{
        font-size: 30px;
    }

    .contact-resume, .contact-email{
        height: 50%;
        width: 100%;
    }

    .change-square{
        width: 300px;
        height: 300px;
        line-height: 270px;
        text-align: center;
        border-radius: 0%;
        border: 15px solid white;
        transition: .2s;
        font-size: 24px;
    }
    
    .contact-resume:hover .change-square{
        border-radius: 50%;
        height: 300px;
        line-height: 270px;
    }
    
    .contact-email:hover .change-square{
        border-radius: 50%;
        height: 300px;
        line-height: 270px;
    }
    

}

@media (max-width: 550px){
    .nav-links{
        font-size: 20px;
    }

    .home-presentation{
        h1{
            font-size: 100px;
        }
    }

    .home-skills{
        p{
        transform: rotate(0deg);
        font-size: 15px;
        }
    }
}

@media (max-width: 400px){

    

    .contact-resume, .contact-email{
        height: 50%;
        width: 100%;
    }

    .change-square{
        width: 200px;
        height: 200px;
        line-height: 170px;
        text-align: center;
        border-radius: 0%;
        border: 15px solid white;
        transition: .2s;
        font-size: 15px;
    }
    
    .contact-resume:hover .change-square{
        border-radius: 50%;
        height: 200px;
        line-height: 170px;
    }
    
    .contact-email:hover .change-square{
        border-radius: 50%;
        height: 200px;
        line-height: 170px;
    }
    

}